.wrapper {
  background: black;
  color: #fff;
  width: 100vw; }
  .wrapper__header {
    display: grid;
    min-height: 100px;
    max-height: 200px;
    padding: 1rem; }
  .wrapper__section {
    padding: calc(1rem * 2); }
  .wrapper__footer {
    padding: 1rem; }

.show-element {
  display: block; }

header {
  display: grid;
  grid-template-columns: 1fr; }

nav {
  color: pink; }

footer {
  background-color: aqua; }
